import * as webSocketActions from '../actions/webSocketActions'
import { applyBackwardPreprocessorFunctionAndPushMessage } from '../actions/messageListActions'

const webSocketMiddleware = () => {
  let socket = null

  const onOpen = store => event => {
    store.dispatch(webSocketActions.wsConnected(event.target.url))
  }

  const onClose = store => () => {
    store.dispatch(webSocketActions.wsDisconnected())
  }

  const onMessage = store => event => {
    const data = JSON.parse(event.data)

    switch (data.actionType) {
      case 'updateOne':
        const messages = [data.message]
        store.dispatch(
          applyBackwardPreprocessorFunctionAndPushMessage(messages)
        )
        break
      default:
        break
    }
  }

  return store => next => action => {
    switch (action.type) {
      case webSocketActions.WS_CONNECT:
        if (socket !== null) {
          socket.close()
        }

        socket = new WebSocket(action.host)
        socket.onmessage = onMessage(store)
        socket.onclose = onClose(store)
        socket.onopen = onOpen(store)
        break
      case webSocketActions.WS_DISCONNECT:
        if (socket !== null) {
          socket.close()
        }

        socket = null
        break
      default:
        return next(action)
    }
  }
}

export default webSocketMiddleware()
